import { Component, Vue } from "vue-property-decorator";
import { FormHeaderComponent } from "../../../_shared/form-header";
import Vuelidate from "vuelidate";
import { required } from "vuelidate/lib/validators";
import "./mail-detail-form.scss";
import { State, Action, Getter } from "vuex-class";
import { GroupButtonForm } from "../../_shared/group-button-form";
import { AlertTriggerForm, ETrigger } from "../../../../store/modules/alert-form";
import { Root } from "../../../../store/root-module";

Vue.use(Vuelidate);
@Component({
    template: require("./mail-detail-form.html"),
    components: {
        "form-header": FormHeaderComponent,
        "group-button-form": GroupButtonForm
    },
    validations: {
        mailSubject: {
            required
        }
    }
})

export class MailDetailFormComponent extends Vue {
    private selectedSubject: string = null;
    private selectedBody: string = null;

    private get optionsSubject() {
        return Root.arrSubjectEmail;
    }

    private get optionsBody() {
        return Root.arrBodyEmail;
    }

    private get mainStatus() {
        return AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.MAIL_DETAILS).status;
    }
    private set mainStatus(newVal) {
        AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.MAIL_DETAILS).status = newVal;
    }

    private get mailBody() {
        return AlertTriggerForm.alertTrigger.MailDetails.Body;
    }
    private set mailBody(newVal) {
        AlertTriggerForm.alertTrigger.MailDetails.Body = newVal;
    }

    private get mailSubject() {
        return AlertTriggerForm.alertTrigger.MailDetails.Subject;
    }
    private set mailSubject(newVal) {
        AlertTriggerForm.alertTrigger.MailDetails.Subject = newVal;
    }

    onUpdateStatut(newStatus: string) {
        this.mainStatus = newStatus;
    }

    addSubject() {
        if (this.mailSubject != null && this.selectedSubject != null) {
            this.mailSubject = this.mailSubject + "%" + this.selectedSubject + "%";
        }
        else {
            if (this.selectedSubject != null) {
                this.mailSubject = "%" + this.selectedSubject + "%";
            }
        }
    }

    addBody() {
        if (this.mailBody != null && this.selectedBody != null) {
            this.mailBody = this.mailBody + "%" + this.selectedBody + "%";
        }
        else if (this.selectedBody != null) {
            this.mailBody = "%" + this.selectedBody + "%";
        }
    }

    onSubmit(): void {
        this.$v.$touch();
        if (!this.$v.mailSubject.$invalid) {           
            this.mainStatus = "filled";
        }
    }

    onReset(): void {
        this.selectedSubject = null;
        this.selectedBody = null;
        AlertTriggerForm.onResetMailDetails();
    }
}
