import { Component, Vue, Prop, Watch } from "vue-property-decorator";
import Vuelidate from "vuelidate";
import "./custom-checkbox.scss";

Vue.use(Vuelidate);
export interface IDictionary {
    Key: IValue;
    Value: Array<IValue>;
}

export interface IValue {
    Id: number;
    Label: string;
    Abbr?: string;
}

export interface ICBDictionary {
    Key: IValue;
    Selected: Array<IValue>;
    AllSelected: boolean;
    Indeterminate: boolean;
}
@Component({
    template: require("./custom-checkbox.html"),
})
export class CustomCheckboxComponent extends Vue {
    @Prop() labelCategoryClass: string;
    @Prop() labelItemClass: string;

    // Prop dicData
    @Prop() dicData: IDictionary;

    // Prop dicCheckbox    
    @Prop() dicCheckbox: ICBDictionary; 

    private generateCustomId(name: string, id: string): string {
        return name.replace(/\s/g, "_") + "_" + id.replace(/\s/g, "_");
    }

    private displayListItems(e: any) {
        if (this.dicData.Value.length > 0) {
            var lstClasses = e.target.classList;
            lstClasses.toggle("up");
            var itemListElem = e.currentTarget.parentElement.parentNode.nextElementSibling;
            itemListElem.classList.toggle("display--items");
        }
    }

    private toggleAll(checked: boolean): void {
        this.dicCheckbox.Selected = checked ? this.dicData.Value.slice() : [];
        this.$emit("onSelectCheckbox");
    }

    @Watch("dicCheckbox", { deep: true })
    private onDicCheckboxChange(newVal: any): void {
        if (newVal.Selected.length === 0) {
            newVal.Indeterminate = false;
            newVal.AllSelected = false;            
        }
        else if (newVal.Selected.length === this.dicData.Value.length) {
            newVal.Indeterminate = false;
            newVal.AllSelected = true;
        }
        else {
            newVal.Indeterminate = true;
            newVal.AllSelected = false;
        }
        this.$emit("onSelectCheckbox");
    }
}
