import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

import "./selected-file.scss";

@Component({
    template: require("./selected-file.html")
})
export class SelectedFileComponent extends Vue {
    @Prop({ required: true }) fileName: string;

    private onRemoveFile(): void {
        this.$emit("onRemove", this.$vnode.key);
    }
}
