import { Component, Vue } from "vue-property-decorator";
import { Root } from "../../../../../../store/root-module";
import { AlertTriggerForm } from "../../../../../../store/modules/alert-form";
import "./condition.scss";

const SendingConditionsModule = AlertTriggerForm.component.SendingConditionsModule;
@Component({
    template: require("./condition.html"),
    components: {

    }
})
export class ConditionComponent extends Vue {
    // Categories
    private get getCategories() {
        return Root.arrCategories;
    }
    private get selectedCategory() {
        if (SendingConditionsModule.selectedCategory === undefined || SendingConditionsModule.selectedCategory === null) {
            return null;
        }
        return SendingConditionsModule.selectedCategory.Id;
    }
    private set selectedCategory(newVal) {
        SendingConditionsModule.selectedCategory = this.getCategories.find(item => item.Id === newVal);
    }

    // Fields
    private get getFields() {
        if (this.selectedCategory === null) {
            return Root.arrFields;
        }
        return Root.arrFields.filter(c => c.Category.Id === this.selectedCategory);
    }
    private get selectedField() {
        if (SendingConditionsModule.selectedField === undefined || SendingConditionsModule.selectedField === null) {
            return null;
        }
        return SendingConditionsModule.selectedField.Id;
    }
    private set selectedField(newVal) {
        SendingConditionsModule.selectedField = this.getFields.find(item => item.Id === newVal);
    }

    // Operators
    private get getOperators() {
        return Root.arrOperators;
    }
    private get selectedOperator() {
        if (SendingConditionsModule.selectedOperator === undefined || SendingConditionsModule.selectedOperator === null) {
            return null;
        }
        return SendingConditionsModule.selectedOperator.Id;
    }
    private set selectedOperator(newVal) {
        SendingConditionsModule.selectedOperator = this.getOperators.find(item => item.Id === newVal);
    }

    private get inputValue() {
        return SendingConditionsModule.inputValue;
    }
    private set inputValue(newVal) {
        SendingConditionsModule.inputValue = newVal;
    }
}
