import { Component, Vue, Watch } from "vue-property-decorator";
import { LayoutComponent } from "../../../../layout";
import PopupComponent from "../../../../common/popup/popup";
import { State } from "vuex-class";

@Component({
    template: require("./preview-form.html"),
    components: {
        "wa-layout": LayoutComponent,
        "popup": PopupComponent,
    }
})
export class PreviewFormComponent extends Vue {

    @State("selectedCategory") selectedCategory: any;
    @State("selectedField") selectedField: any;
    @State("selectedOperator") selectedOperator: any;
    @State("inputValue") inputValue: string;

    mounted() {
        this.init();
    }

    private init(): void {
    }
}
