import { Component, Vue, Watch } from "vue-property-decorator";
import { LayoutComponent } from "../../../layout";
import PopupComponent from "../../../common/popup/popup";
import { MainInformationFormComponent } from "../../main-information-form/_/main-information-form";
import { MailDetailFormComponent } from "../../mail-detail-form/_/mail-detail-form";
import { AttachmentComponent } from "../../attachment-section/_/attachment-section";
import { DateActivationRangeComponent } from "../../date-activation-range-section/_/date-activation-range-section";
import { MailRecipientComponent } from "../../mail-recipients/_/mail-recipients";
import { SendingConditionsFormComponent } from "../../sending-conditions-form";
import { MailStructureFormComponent } from "../../mail-structure-form/_/mail-structure-form";
import "./triggered-alert-page.scss";
import Axios, { AxiosResponse } from "axios";
import { StaticCommonFunc } from "../../../../util/static-common-function";
import { isNullOrUndefined } from "util";
import { Root } from "../../../../store/root-module";
import { AlertTriggerForm, ETrigger, EInitStatus } from "../../../../store/modules/alert-form";

export const eventBus = new Vue();
@Component({
    template: require("./triggered-alert-page.html"),
    components: {
        "wa-layout": LayoutComponent,
        "mail-detail-form": MailDetailFormComponent,
        "main-information-form": MainInformationFormComponent,
        "attachment-section": AttachmentComponent,
        "date-activation-range-section": DateActivationRangeComponent,
        "mail-recipient-component": MailRecipientComponent,
        "sending-conditions-form": SendingConditionsFormComponent,
        "popup": PopupComponent,
        "mail-structure-form": MailStructureFormComponent
    }
})
export class TriggeredAlertPageComponent extends Vue {
    private processId: number = -1;
    private isShowPage: boolean = false;
    private isProcessing: boolean = false;
    private mode: string = process.env.ENV;
    private popTitle: string = "Cancel Process Createion";
    private showCancelPopup: boolean = false;
    private checkCount = 0;

    mounted() {
        if (Root.linkAccount == "") {
            this.$router.push({ name: "LinkAccount" });
        }
    }

    public get getChangeStatus() {
        for (var i = 0; i < AlertTriggerForm.arrStatusForm.length; i++) {
            var iItem = AlertTriggerForm.arrStatusForm[i];
            if (iItem.status === "active") {
                for (var j = i + 1; j < AlertTriggerForm.arrStatusForm.length; j++) {
                    var jItem = AlertTriggerForm.arrStatusForm[j];
                    if (jItem.status === "empty") {
                        break;
                    } else {
                        jItem.status = "empty";
                    }
                }
                break;
            }
            var k = i + 1;
            if (iItem.status === "filled" && k < AlertTriggerForm.arrStatusForm.length) {
                var kItem = AlertTriggerForm.arrStatusForm[k];
                if (kItem.status === "empty") {
                    kItem.status = "active";
                    break;
                }
            }
        }
        return AlertTriggerForm.arrStatusForm.filter(item => ["filled", "recapNonEdit", "recapSpecial"].indexOf(item.status) > -1).length === AlertTriggerForm.arrStatusForm.length;
    }

    private get isDisabled() {
        return this.isProcessing;
    }

    private onClickCancel() {
        this.showCancelPopup = true;
    }

    private resultCancel(value) {
        if (value) {
            this.goToHomePage();
        }

        this.showCancelPopup = false;
    }

    private async created() {
        eventBus.$on("showDeleteModal", (_showCancel: boolean) => {
            this.showCancelPopup = _showCancel;
        });

        this.isShowPage = false;
        // Recap
        if (this.$route.params.id !== undefined && this.$route.params.id != null && this.$route.params.id !== "" && this.$route.params.id !== "-1") {
            this.processId = parseInt(this.$route.params.id);
            await Promise.all([
                this.asyncLoadMasterData(false),
                this.loadData(),
                AlertTriggerForm.fetchDataArrMailStructure()
            ]).then((resp) => {
                if (resp[0] && resp[2] && resp[1]) {
                    this.initForm(resp[1]);
                }
            });
        } else { // Create new
            await this.asyncLoadMasterData(true);
        }
        this.isShowPage = true;
    }

    private async asyncLoadMasterData(isCreateNew: boolean) {
        if (isCreateNew) {
            await Promise.all([
                Root.fetchClients(),
                Root.fetchArlertType(),
                Root.fetchModuleType(),
                AlertTriggerForm.fetchDataArrMailStructure()
            ]);
            Root.fetchCategories();
            Root.fetchOperators();
            Root.fetchFields();
            Root.fetchSubjectEmail();
            Root.fetchBodyEmail();
            Root.fetchEmailRecipientType();
            Root.fetchThirdPartyRole();
        } else {
            await Promise.all([
                Root.fetchClients(),
                Root.fetchArlertType(),
                Root.fetchModuleType(),
                Root.fetchCategories(),
                Root.fetchOperators(),
                Root.fetchFields(),
                Root.fetchSubjectEmail(),
                Root.fetchBodyEmail(),
                Root.fetchEmailRecipientType(),
                Root.fetchThirdPartyRole()
            ]);
        }
        return true;
    }

    private onClickSave() {
        var statusDateActivationRange = AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.DATE_ACTIVATION_RANGE).status;
        var kindSaveDate = -1;
        if (this.processId !== -1) {
            if (statusDateActivationRange === "filled") {
                kindSaveDate = 1;
            } else if (statusDateActivationRange === "recapSpecial") {
                kindSaveDate = 2;
            }
        }
        this.isProcessing = true;
        Axios.post("/ClientAlerting/Alert/SaveData", { alertTrigger: AlertTriggerForm.alertTrigger, kindSaveDate: kindSaveDate }).then((resp) => {
            if (resp.data.dResult) {
                this.goToHomePage();
            }
            this.isProcessing = false;
        }).catch((reason) => {
            console.log(reason);
            this.isProcessing = false;
        });
    }

    private async loadData() {
        return await Axios.get("/ClientAlerting/Alert/LoadData", { params: { alertId: this.processId } }).then((response: AxiosResponse) => {
            if (response.data.dResult) {
                return response.data.dAlertTrigger;
            }
            return null;
        }).catch(function (reason) {
            console.log(reason);
            return null;
        });
    }

    private initForm(dAlertTrigger) {
        AlertTriggerForm.fetchDataAlertTrigger(dAlertTrigger);
        var currentDate = new Date();
        var cvertMinDate = StaticCommonFunc.convertFromJsonDateToJsDate(String(AlertTriggerForm.alertTrigger.MainInformation.ActiveMinDate));
        var cvertMaxDate = StaticCommonFunc.convertFromJsonDateToJsDate(String(AlertTriggerForm.alertTrigger.MainInformation.ActiveMaxDate));
        if (cvertMinDate <= currentDate && (isNullOrUndefined(cvertMaxDate) || isNaN(cvertMaxDate.getDate()) || currentDate <= cvertMaxDate)) {
            AlertTriggerForm.fetchInitStatusForm(EInitStatus.RECAP_NON_EDIT);
        } else {
            AlertTriggerForm.fetchInitStatusForm(EInitStatus.RECAP_EDIT);
        }
    }

    private goToHomePage(): void {
        this.$router.push({ name: "HomePage" });
    }

    @Watch("isProcessing")
    @Watch("isShowPage")
    private isLoading(): void {
        Root.fetchShowLoading(this.isProcessing || !this.isShowPage);
    }
}
