import { Component, Vue, Watch } from "vue-property-decorator";
import Vuelidate from "vuelidate";
import { required } from "vuelidate/lib/validators";
import { FormHeaderComponent } from "../../../_shared/form-header";
import { GroupButtonForm } from "../../_shared/group-button-form";
import { AddConditionComponent } from "../add-condition";
import { PreviewFormComponent } from "../preview-form";
import { IStrCondition } from "../../../../store/modules/alert-form/components/sending-conditions-form";
import { AlertTriggerForm, ETrigger } from "../../../../store/modules/alert-form";
import { Root } from "../../../../store/root-module";
import "./sending-conditions-form.scss";

Vue.use(Vuelidate);
const SendingConditionsModule = AlertTriggerForm.component.SendingConditionsModule;
@Component({
    template: require("./sending-conditions-form.html"),
    components: {
        "form-header": FormHeaderComponent,
        "add-condition": AddConditionComponent,
        "preview-form": PreviewFormComponent,
        "group-button-form": GroupButtonForm
    },
    validations: {
        arrStrConditions: {
            required
        }
    }
})
export class SendingConditionsFormComponent extends Vue {
    private selectedStrCondition: Array<IStrCondition> = [];

    private get arrConditions() {
        return AlertTriggerForm.alertTrigger.SendingConditions;
    }
    private set arrConditions(newVal) {
        AlertTriggerForm.alertTrigger.SendingConditions = newVal;
    }

    private get sendingConditionsStatus() {
        return AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.SENDING_CONDITIONS).status;
    }
    private set sendingConditionsStatus(newVal) {
        AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.SENDING_CONDITIONS).status = newVal;
    }

    private get arrStrConditions() {
        return SendingConditionsModule.arrStrConditions;
    }
    private set arrStrConditions(newVal) {
        SendingConditionsModule.arrStrConditions = newVal;
    }

    private onUpdateStatus(newStatus: string) {
        this.sendingConditionsStatus = newStatus;
    }

    private onSubmit(): void {
        this.$v.arrStrConditions.$touch();
        if (!this.$v.arrStrConditions.$invalid) {
            this.sendingConditionsStatus = "filled";
        }
    }

    private onReset(): void {
        AlertTriggerForm.onResetSendingConditions();
        this.$v.arrStrConditions.$reset();
    }

    private onPreviewForm(): void {
        //this.$router.push({ name: "previewForm" });
    }

    private onRemoveItem(): void {
        this.selectedStrCondition.forEach(item => {
            this.arrConditions.splice(item.coreIndex, 1);
            var indexConditionRemove = this.arrStrConditions.findIndex(c => c.coreIndex === item.coreIndex);
            this.arrStrConditions.splice(indexConditionRemove, 1);
        });
    }
    
    @Watch("arrConditions")
    @Watch("sendingConditionsStatus")
    private getArrStrConditions(): void {
        this.arrStrConditions = [];
        if (this.arrConditions.length > 0 || this.sendingConditionsStatus === "filled") {
            this.arrConditions.forEach((c, index) => {
                var field = Root.arrFields.find(x => x.Id === c.SourceField.Id);
                var category = Root.arrCategories.find(x => x.Id === field.Category.Id);
                var operator = Root.arrOperators.find(x => x.Id === c.Operator.Id);
                var firstSplit = category.Label + " " + field.Label + " " + operator.Label;
                if (this.arrStrConditions.length > 0 && this.arrStrConditions.find(x => x.firstSplit === firstSplit && firstSplit.indexOf("=") > -1)) {
                    this.arrStrConditions.find(x => x.firstSplit === firstSplit && firstSplit.indexOf("=") > 0).secondSpit += " or " + c.Value;
                } else {
                    this.arrStrConditions.push({
                        coreIndex: index,
                        firstSplit: firstSplit,
                        secondSpit: c.Value
                    });
                }
            });
        }
    }

    @Watch("arrStrConditions")
    private displayStrConditions(): Array<any> {
        var optStrConditions: Array<any> = [];
        let preText = this.sendingConditionsStatus === "active" ? " -" : "";
        if (this.arrStrConditions.length > 0) {
            this.arrStrConditions.forEach((item, index) => {
                optStrConditions.push({
                    value: index,
                    text: preText + item.firstSplit + " " + item.secondSpit
                });
            });
        }
        return optStrConditions;
    }
}
