import { Component, Vue } from "vue-property-decorator";
import "./attachment-section.scss";
import { FormHeaderComponent } from "../../../_shared/form-header";
import { GroupButtonForm } from "../../_shared/group-button-form";
import { ButtonIconAddComponent } from "../../../common/button-icon-add";
import { SelectedFileComponent } from "./list-selected-files";
import { AlertTriggerForm, ETrigger } from "../../../../store/modules/alert-form";

@Component({
    template: require("./attachment-section.html"),
    components: {
        "form-header": FormHeaderComponent,
        "group-button-form": GroupButtonForm,
        "btn-icon-add": ButtonIconAddComponent,
        "selected-file": SelectedFileComponent
    }
})
export class AttachmentComponent extends Vue {
    private documentType: any = null;

    private get attachmentStatus() {
        return AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.ATTACHMENTS).status;
    }
    private set attachmentStatus(newVal) {
        AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.ATTACHMENTS).status = newVal;
    }

    private documentTypes: { value: string, text: string, disabled?: boolean }[] = [
        { value: null, text: "Document type", disabled: true },
        { value: ".pdf", text: ".Pdf" },
        { value: ".excel", text: ".Excel" },
        { value: "word", text: ".Word" },
        { value: ".jpg", text: ".jpg" }
    ];

    get selectedDocument() {
        return AlertTriggerForm.alertTrigger.Attachments;
    }
    set selectedDocument(value) {
        AlertTriggerForm.alertTrigger.Attachments = value;
    }

    onSubmit() {
        this.attachmentStatus = "filled";
    }

    onRemoveFile(key: number): void {
        this.selectedDocument.splice(key, 1);
    }

    addAttachment(event): void {
        const that = this;
        let selectedFile = event.target.files[0] as File;
        let contentBinary = "";
        if (selectedFile) {
            var r = new FileReader();
            var spanTime = new Date().toISOString().replace(/[-T:]/gi, "").split(".")[0];
            var arrFileName = selectedFile.name.split(".");
            var extenstion = arrFileName.pop();
            var newFileName = arrFileName.join(".") + "_" + spanTime + "." + extenstion;
            r.onload = function (e) {
                contentBinary = (e.target as any).result;
                that.selectedDocument.push({ Id: null, AlertEmail: null, FileName: newFileName, FilePath: "", Data64: contentBinary })
            }
            r.readAsDataURL(selectedFile as any);
        }
    }

    onReset(): void {
        AlertTriggerForm.onResetAttachment();
    }

    onUpdateStatus(newStatus: string) {
        this.attachmentStatus = newStatus;
    }
}
