import { Component, Vue, Prop } from "vue-property-decorator";
import "./client-info.scss";
import { Root } from "../../../../../store/root-module";
import { AlertTriggerForm } from "../../../../../store/modules/alert-form";

@Component({
    template: require("./client-info.html"),
    components: {

    }
})
export class ClientInfoComponent extends Vue {
    @Prop() status: string;
    @Prop() validation: any;    

    private get processName() {
        return AlertTriggerForm.component.MainInformationModule.clientInfo.processName;
    }
    private set processName(newVal) {
        AlertTriggerForm.component.MainInformationModule.clientInfo.processName = newVal;
    }

    private get selectedClient() {
        return AlertTriggerForm.component.MainInformationModule.clientInfo.selectedClient;
    }
    private set selectedClient(newVal) {
        AlertTriggerForm.component.MainInformationModule.clientInfo.selectedClient = newVal;
    }

    existProcess: boolean = false;

    get arrClients() {
        return Root.arrClients;
    }

    get selectedText() {
        if (this.arrClients.length === 0 || this.selectedClient === null) {
            return "";
        }
        return this.arrClients.find(item => item.Code === this.selectedClient).Name;
    }
}
