import { Component, Vue, Watch } from "vue-property-decorator";
import Vuelidate from "vuelidate";
import { FormHeaderComponent } from "../../../_shared/form-header";
import { GroupButtonForm } from "../../_shared/group-button-form";
import { ButtonIconAddComponent } from "../../../common/button-icon-add";
import { AlertTriggerForm, ETrigger } from "../../../../store/modules/alert-form";
import { Root } from "../../../../store/root-module";
import "./mail-recipients.scss";

Vue.use(Vuelidate);

interface IMailRecipients {
    lstMailTo: Array<IFormatMail>;
    lstMailCc: Array<IFormatMail>;
    lstMailCci: Array<IFormatMail>;
}
interface IFormatMail {
    mail: string;
    role?: number
}
@Component({
    template: require("./mail-recipients.html"),
    components: {
        "form-header": FormHeaderComponent,
        "group-button-form": GroupButtonForm,
        "btn-icon-add": ButtonIconAddComponent
    },
    validations: {
        mailTo: {
            requiredMail: function (value: string, vm) {
                return vm.mailToRequired(value);
            },
            mustBeEmail: function (value: string, vm) {
                return vm.updateMailTo(value);
            }
        },
        mailCc: {
            mustBeEmail: function (value: string, vm) {
                return vm.updateMailCc(value);
            }
        },
        mailCci: {
            mustBeEmail: function (value: string, vm) {
                return vm.updateMailCci(value);
            }
        }
    }
})

export class MailRecipientComponent extends Vue {
    mailRecipients: IMailRecipients = { lstMailCc: [], lstMailCci: [], lstMailTo: [] };
    mailTo: string = "";
    mailCc: string = "";
    mailCci: string = "";
    isSubmit: boolean = false;
    isThirdPartyRole: boolean = false;
    selectRecipientType?: number = null;
    selectThirdPartyRole?: number = null;
    thirdPartyRoleSelectedList: { thirdPartyId: number, recipientTypeId: number }[] = [];

    get arrRecipientType() {
        return Root.arrRecipientType;
    }

    get arrThirdPartyRole() {
        return Root.arrThirdPartyRole;
    }

    get mailRecipientStatus() {
        return AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.MAIL_RECIPIENTS).status;
    }
    set mailRecipientStatus(newVal) {
        AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.MAIL_RECIPIENTS).status = newVal;
    }

    get mailtoList() {
        return Array.from(new Set(this.mailRecipients.lstMailTo.map(s => s.mail)))
            .map(mail => {
                return {
                    mail: mail,
                    role: this.mailRecipients.lstMailTo.find(s => s.mail === mail).role
                };
            });
    }

    get mailccList() {
        return Array.from(new Set(this.mailRecipients.lstMailCc.map(s => s.mail)))
            .map(mail => {
                return {
                    mail: mail,
                    role: this.mailRecipients.lstMailCc.find(s => s.mail === mail).role
                };
            });
    }

    get mailcciList() {
        return Array.from(new Set(this.mailRecipients.lstMailCci.map(s => s.mail)))
            .map(mail => {
                return {
                    mail: mail,
                    role: this.mailRecipients.lstMailCci.find(s => s.mail === mail).role
                };
            });
    }

    get storeMailRecipients() {
        return AlertTriggerForm.alertTrigger.MailRecipients;
    }
    set storeMailRecipients(newVal) {
        AlertTriggerForm.alertTrigger.MailRecipients = newVal;
    }

    @Watch("mailRecipientStatus")
    @Watch("storeMailRecipients")
    processGetDataFromStore(): void {
        console.log(this.isSubmit);
        if (!this.isSubmit) {
            console.log(this.isSubmit);
            if (["empty", "active"].indexOf(this.mailRecipientStatus) < 0) {
                if (this.storeMailRecipients.length > 0) {
                    console.log(this.isSubmit);
                    var typeIdTO = this.arrRecipientType.find(item => item.Code === "MTO").Id;
                    var typeIdCC = this.arrRecipientType.find(item => item.Code === "MCC").Id;
                    var typeIdCCI = this.arrRecipientType.find(item => item.Code === "CCI").Id;
                    this.receiveDataFromStore();
                    this.mailRecipients.lstMailTo.forEach((item) => {
                        if (item.role !== null) {
                            this.thirdPartyRoleSelectedList.push({ thirdPartyId: item.role, recipientTypeId: typeIdTO });
                        }
                    });
                    this.mailRecipients.lstMailCc.forEach((item) => {
                        if (item.role !== null) {
                            this.thirdPartyRoleSelectedList.push({ thirdPartyId: item.role, recipientTypeId: typeIdCC });
                        }
                    });
                    this.mailRecipients.lstMailCci.forEach((item) => {
                        if (item.role !== null) {
                            this.thirdPartyRoleSelectedList.push({ thirdPartyId: item.role, recipientTypeId: typeIdCCI });
                        }
                    });
                }
            }
        }
    }

    mailToRequired() {
        if (this.mailRecipients.lstMailTo.length == 0) {
            if (this.mailTo.trim().length == 0) {
                return false;
            }
        }
        return true;
    }

    isValidEmail(value: string) {
        var reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        var resultCheck = true;
        var resultTemp = [];

        resultTemp = value.split(";");
        for (var i = 0; i < resultTemp.length; i++) {
            if (resultTemp[i].trim() != "") {
                resultCheck = reg.test(String(resultTemp[i].trim()).toLowerCase());
                if (!resultCheck) {
                    return false;
                }
            }
        }

        return true;
    }

    updateMailTo(value: string) {
        var self = this;
        var check = self.isValidEmail(value);
        if (check && (self.mailTo.indexOf(";") > 0)) {
            var lstMailTemp = self.getMailList(self.mailTo);
            lstMailTemp.forEach(function (item) {
                self.mailRecipients.lstMailTo.push({ mail: item.mail, role: null });
            });
            self.mailTo = "";
        }

        return check;
    }

    updateMailCc(value: string) {
        var self = this;
        var check = self.isValidEmail(value);
        if (check && (self.mailCc.indexOf(";") > 0)) {
            var lstMailTemp = self.getMailList(self.mailCc);
            lstMailTemp.forEach(function (item) {
                self.mailRecipients.lstMailCc.push({ mail: item.mail, role: null });
            });
            self.mailCc = "";
        }

        return check;
    }

    updateMailCci(value: string) {
        var self = this;
        var check = self.isValidEmail(value);
        if (check && (self.mailCci.indexOf(";") > 0)) {
            var lstMailTemp = self.getMailList(self.mailCci);
            lstMailTemp.forEach(function (item) {
                self.mailRecipients.lstMailCci.push({ mail: item.mail, role: null });
            });
            self.mailCci = "";
        }

        return check;
    }

    deleteMailToItem(mail) {
        let arr = this.mailRecipients.lstMailTo.filter(item => item.mail !== mail);
        this.mailRecipients.lstMailTo = arr;
    }

    deleteMailCcItem(mail) {
        let arr = this.mailRecipients.lstMailCc.filter(item => item.mail !== mail);
        this.mailRecipients.lstMailCc = arr;
    }

    deleteMailCciItem(mail) {
        let arr = this.mailRecipients.lstMailCci.filter(item => item.mail !== mail);
        this.mailRecipients.lstMailCci = arr;
    }

    onUpdateIsThirdPartyRoleStatus(newStatus: boolean) {
        this.isThirdPartyRole = newStatus;
        if (newStatus) {
            this.selectRecipientType = null;
            this.selectThirdPartyRole = null;
        }
    }

    onUpdateStatus(newStatus: string) {
        this.mailRecipientStatus = newStatus;
        this.onUpdateIsThirdPartyRoleStatus(false);
        this.mailTo = "";
        this.mailCc = "";
        this.mailCci = "";
        AlertTriggerForm.onResetMailRecipients();
        this.isSubmit = false;
    }

    onAddThirdPartyRole() {
        var self = this;

        if (self.selectRecipientType == null || self.selectThirdPartyRole == null) {
            return;
        }

        if (self.selectRecipientType === this.arrRecipientType.find(item => item.Code === "MTO").Id) {
            self.mailRecipients.lstMailTo.push({
                mail: self.arrThirdPartyRole.find(item => item.Id === self.selectThirdPartyRole).Label,
                role: self.selectThirdPartyRole
            });
        } else if (self.selectRecipientType === this.arrRecipientType.find(item => item.Code === "MCC").Id) {
            self.mailRecipients.lstMailCc.push({
                mail: self.arrThirdPartyRole.find(item => item.Id === self.selectThirdPartyRole).Label,
                role: self.selectThirdPartyRole
            });
        } else if (self.selectRecipientType === this.arrRecipientType.find(item => item.Code === "CCI").Id) {
            self.mailRecipients.lstMailCci.push({
                mail: self.arrThirdPartyRole.find(item => item.Id === self.selectThirdPartyRole).Label,
                role: self.selectThirdPartyRole
            });
        }

        var existItem = this.thirdPartyRoleSelectedList.filter(item => item.recipientTypeId === self.selectRecipientType && item.thirdPartyId === self.selectThirdPartyRole);
        if (existItem.length === 0) {
            this.thirdPartyRoleSelectedList.push({ recipientTypeId: self.selectRecipientType, thirdPartyId: self.selectThirdPartyRole });
        }
    }

    getMailList(mailInput: string) {
        var resultTemp = [];
        var lstMail = [];
        var lstMailDup = [];
        var lstMailFinal = [];
        // Get list email
        resultTemp = mailInput.split(";");
        for (var i = 0; i < resultTemp.length; i++) {
            if (resultTemp[i].trim() != "") {
                lstMail.push(resultTemp[i].trim());
            }
        }
        // Remove duplicate email
        lstMail.forEach(function (item) {
            if (lstMailDup.indexOf(item) < 0) {
                lstMailDup.push(item);
            }
        });
        // Return final list
        lstMailDup.forEach(function (item) {
            lstMailFinal.push({ mail: item });
        });

        return lstMailFinal;
    }

    getFinalListMail(lstInput: { mail: string }[], textInput: string) {
        var self = this;
        var lstMail = [];
        var lstMailDup = [];
        var lstMailFinal = lstInput;
        // Get form input control
        var lstMailTemp = self.getMailList(textInput);
        lstMailTemp.forEach(function (item) {
            lstMailFinal.push(item);
        });
        // Remove duplicate
        lstMailFinal.forEach(function (item) {
            lstMail.push(item.mail);
        });
        lstMail.forEach(function (item) {
            if (lstMailDup.indexOf(item) < 0) {
                lstMailDup.push(item);
            }
        });
        lstMailFinal = [];
        lstMailDup.forEach(function (item) {
            lstMailFinal.push({ mail: item });
        });

        return lstMailFinal;
    }

    onSubmit(): void {
        var self = this;
        self.$v.$touch();
        if (!self.$v.mailTo.$invalid && !self.$v.mailCc.$invalid && !self.$v.mailCci.$invalid) {
            self.getFinalListMail(self.mailRecipients.lstMailTo, self.mailTo);
            self.getFinalListMail(self.mailRecipients.lstMailCc, self.mailCc);
            self.getFinalListMail(self.mailRecipients.lstMailCci, self.mailCci);
            self.mailTo = "";
            self.mailCc = "";
            self.mailCci = "";
            self.isSubmit = true;
            self.mailRecipientStatus = "filled";
            self.updateAlertTriggerMailRecipients();
        }
    }

    onReset(): void {
        this.mailTo = "";
        this.mailCc = "";
        this.mailCci = "";
        this.isThirdPartyRole = false;
        this.selectRecipientType = null;
        this.selectThirdPartyRole = null;
        this.isSubmit = false;
        this.thirdPartyRoleSelectedList = [];
        this.mailRecipients = { lstMailCc: [], lstMailCci: [], lstMailTo: [] };
        AlertTriggerForm.onResetMailRecipients();
    }

    receiveDataFromStore(): void {
        this.mailRecipients = { lstMailCc: [], lstMailCci: [], lstMailTo: [] };
        // Mail TO
        this.storeMailRecipients.filter(item => item.RecipientTypeId === this.arrRecipientType.find(type => type.Code === "MTO").Id)
            .forEach(item => {
                this.mailRecipients.lstMailTo.push({
                    mail: item.ThirdPartyRoleId === null ? item.EmailAddress
                        : this.arrThirdPartyRole.find(role => role.Id === item.ThirdPartyRoleId).Label,
                    role: item.ThirdPartyRoleId
                });
            });
        // Mail CC
        this.storeMailRecipients.filter(item => item.RecipientTypeId === this.arrRecipientType.find(type => type.Code === "MCC").Id)
            .forEach(item => {
                this.mailRecipients.lstMailCc.push({
                    mail: item.ThirdPartyRoleId === null ? item.EmailAddress
                        : this.arrThirdPartyRole.find(role => role.Id === item.ThirdPartyRoleId).Label,
                    role: item.ThirdPartyRoleId
                });
            });
        // Mail CCI
        this.storeMailRecipients.filter(item => item.RecipientTypeId === this.arrRecipientType.find(type => type.Code === "CCI").Id)
            .forEach(item => {
                this.mailRecipients.lstMailCci.push({
                    mail: item.ThirdPartyRoleId === null ? item.EmailAddress
                        : this.arrThirdPartyRole.find(role => role.Id === item.ThirdPartyRoleId).Label,
                    role: item.ThirdPartyRoleId
                });
            });
    }

    updateAlertTriggerMailRecipients() {
        // Mail TO
        this.mailRecipients.lstMailTo.forEach(item => {
            this.storeMailRecipients.push({
                Id: null,
                AlertEmail: null,
                RecipientType: this.arrRecipientType.find(type => type.Code === "MTO"),
                RecipientTypeId: this.arrRecipientType.find(type => type.Code === "MTO").Id,
                EmailAddress: item.mail,
                ThirdPartyRoleId: item.role
            });
        });
        // Mail CC
        this.mailRecipients.lstMailCc.forEach(item => {
            this.storeMailRecipients.push({
                Id: null,
                AlertEmail: null,
                RecipientType: this.arrRecipientType.find(type => type.Code === "MCC"),
                RecipientTypeId: this.arrRecipientType.find(type => type.Code === "MCC").Id,
                EmailAddress: item.mail,
                ThirdPartyRoleId: item.role
            });
        });
        // Mail CCI
        this.mailRecipients.lstMailCci.forEach(item => {
            this.storeMailRecipients.push({
                Id: null,
                AlertEmail: null,
                RecipientType: this.arrRecipientType.find(type => type.Code === "CCI"),
                RecipientTypeId: this.arrRecipientType.find(type => type.Code === "CCI").Id,
                EmailAddress: item.mail,
                ThirdPartyRoleId: item.role
            });
        });
    }
}
