import { Component, Vue, Watch } from "vue-property-decorator";
import Vuelidate from "vuelidate";
import { required } from "vuelidate/lib/validators";
import { ClientInfoComponent } from "../client-info";
import { StyledFormRadioComponent, IOption, defaultValueOption } from "../../../_shared/styled-form-radio";
import { FormHeaderComponent } from "../../../_shared/form-header";
import { GroupButtonForm } from "../../_shared/group-button-form";
import { AlertTriggerForm, ETrigger } from "../../../../store/modules/alert-form";
import { IModuleType, IAlertType } from "../../../../model/io";
import { isProcessExist } from "../../../../util/validators";
import { Root } from "../../../../store/root-module";
import "./main-information-form.scss";

Vue.use(Vuelidate);
const initData = {
    moduleType: <IModuleType>{
        Code: "",
        Id: null,
        Label: ""
    },
    alertType: <IAlertType>{
        Code: "",
        Id: null,
        Label: ""
    }
}
@Component({
    template: require("./main-information-form.html"),
    components: {
        "styled-form-radio": StyledFormRadioComponent,
        "form-header": FormHeaderComponent,
        "client-info": ClientInfoComponent,
        "group-button-form": GroupButtonForm
    },
    validations: {
        form: {
            clientInfo: {
                processName: {
                    required
                },
                selectedClient: {
                    required
                }
            }
        },
        selectedModuled: {
            required,
            label: {
                required
            },
            id: {
                required
            }
        },
        selectedType: {
            required,
            label: {
                required
            },
            id: {
                required
            }
        }

    }
})
export class MainInformationFormComponent extends Vue {
    moduleTransport: IModuleType = JSON.parse(JSON.stringify(initData.moduleType));
    moduleShipment: IModuleType = JSON.parse(JSON.stringify(initData.moduleType));
    typeTrigger: IAlertType = JSON.parse(JSON.stringify(initData.alertType));
    typeScheduled: IAlertType = JSON.parse(JSON.stringify(initData.alertType));

    readonly optionIcon = {
        transport: "fas fa-plane fa-lg",
        shipment: "fas fa-map-marker-alt fa-lg",
        trigger: "fas fa-bolt fa-lg",
        schedule: "fas fa-clock fa-lg"
    };
    selectedModuled: IOption = JSON.parse(JSON.stringify(defaultValueOption));
    selectedType: IOption = JSON.parse(JSON.stringify(defaultValueOption));

    private get GBMainInformation() {
        return AlertTriggerForm.alertTrigger.MainInformation;
    }
    private set GBMainInformation(newVal) {
        AlertTriggerForm.alertTrigger.MainInformation = newVal;
    }

    private get form() {
        return AlertTriggerForm.component.MainInformationModule;
    }
    private set form(newVal) {
        AlertTriggerForm.component.MainInformationModule = newVal;
    }

    private get mainStatus() {
        return AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.MAIN_INFORMATION).status;
    }
    private set mainStatus(newVal) {
        AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.MAIN_INFORMATION).status = newVal;
    }

    private get arrModuleType() {
        return Root.arrModuleType;
    }

    private get arrAlertType() {
        return Root.arrAlertType;
    }

    @Watch("arrModuleType")
    @Watch("arrAlertType")
    private init(): void {
        if (this.arrModuleType.length > 0) {
            this.moduleTransport = this.arrModuleType.find(item => item.Code === "TRE");
            this.moduleShipment = this.arrModuleType.find(item => item.Code === "SHI");
        }
        if (this.arrAlertType.length > 0) {
            this.typeTrigger = this.arrAlertType.find(item => item.Code === "TRI");
            this.typeScheduled = this.arrAlertType.find(item => item.Code === "SCH");
        }
    }

    @Watch("mainStatus")
    @Watch("GBMainInformation")
    private alertTypePropertyChange(): void {
        if (["filled", "recapNonEdit"].indexOf(this.mainStatus) > -1) {
            if (this.GBMainInformation.ModuleType !== null && this.GBMainInformation.AlertType !== null) {
                var selectedModuleType = this.arrModuleType.find(item => item.Id === this.GBMainInformation.ModuleType.Id);
                var selectedAlertType = this.arrAlertType.find(item => item.Id === this.GBMainInformation.AlertType.Id);

                this.selectedType = {
                    icon: selectedAlertType.Code === "TRI" ? this.optionIcon.trigger : this.optionIcon.schedule,
                    label: selectedAlertType.Label,
                    id: selectedAlertType.Id,
                    isError: false
                };

                this.selectedModuled = {
                    icon: selectedModuleType.Code === "TRE" ? this.optionIcon.transport : this.optionIcon.shipment,
                    label: selectedModuleType.Label,
                    id: selectedModuleType.Id,
                    isError: false
                };

                this.form.clientInfo.processName = this.GBMainInformation.Name;
                this.form.clientInfo.selectedClient = this.GBMainInformation.Transac;
            }
        }
    }

    onUpdateStatus(newStatus: string) {
        this.mainStatus = newStatus;
    }

    async onSubmit() {
        this.$v.form.$touch();
        this.selectedModuled.isError = this.$v.selectedModuled.$invalid;
        this.selectedType.isError = this.$v.selectedType.$invalid;

        if (!this.$v.form.clientInfo.$invalid && !this.selectedModuled.isError && !this.selectedType.isError) {
            let existProcess = false;
            let procesName = this.form.clientInfo.processName;
            if (this.GBMainInformation.Id == 0 || (this.GBMainInformation.Id != 0 && procesName != this.GBMainInformation.Name)) {
                existProcess = await this.checkExistProcess();
            }
            (<ClientInfoComponent>this.$refs.clientInfo).existProcess = existProcess;
            if (!existProcess) {
                this.mainStatus = "filled";
                Root.fetchShowLoading(false);
                // Build data to server
                this.GBMainInformation.Name = this.form.clientInfo.processName;
                this.GBMainInformation.Transac = this.form.clientInfo.selectedClient;
                this.GBMainInformation.ModuleType = {
                    Id: this.selectedModuled.id,
                    Label: this.selectedModuled.label
                }
                this.GBMainInformation.AlertType = {
                    Id: this.selectedType.id,
                    Label: this.selectedType.label
                }
            }
        }
    }

    private async checkExistProcess() {
        Root.fetchShowLoading(true);
        let existProcess: boolean = false;
        await isProcessExist(this.form.clientInfo.processName).then((result) => {
            existProcess = result;
            Root.fetchShowLoading(false);
        });

        return existProcess;
    }

    onReset(): void {
        AlertTriggerForm.onResetMainInformation();
        this.selectedModuled = JSON.parse(JSON.stringify(defaultValueOption));
        this.selectedType = JSON.parse(JSON.stringify(defaultValueOption));
        (<ClientInfoComponent>this.$refs.clientInfo).existProcess = false;
        this.$v.form.clientInfo.$reset();
        this.$v.selectedModuled.$reset();
        this.$v.selectedType.$reset();
    }
}
