import { VuexModule, MutationAction, getModule, Mutation, Action, Module } from "vuex-module-decorators";
import { ISendingConditionsForm } from "./components/sending-conditions-form";
import { IMailStructureForm, IStructureData } from "./components/mailStructureForm";
import { IMainInformationForm } from "./components/main-information-form";
import { IAlertTrigger } from "../../../model/io";
import { StaticCommonFunc } from "../../../util/static-common-function";
import { store } from "../..";
import Axios from "axios";
import { StyledFormRadioComponent } from "../../../components/_shared/styled-form-radio";


export enum ETrigger {
    MAIN_INFORMATION,
    SENDING_CONDITIONS,
    MAIL_RECIPIENTS,
    MAIL_DETAILS,
    MAIL_STRUCTURE,
    ATTACHMENTS,
    DATE_ACTIVATION_RANGE
}

export enum EInitStatus { CREATE, RECAP_NON_EDIT, RECAP_EDIT }

export interface IStatusComponent {
    nameComponent: ETrigger;
    status: string;
    nextComponent: ETrigger;
}

interface IAlertTriggerFormModule {
    alertTrigger: IAlertTrigger;
    arrStatusForm: Array<IStatusComponent>;

    component: {
        MainInformationModule: IMainInformationForm,
        SendingConditionsModule: ISendingConditionsForm,
        MailStructureModule: IMailStructureForm
    };
}

const SendingConditionsModule: ISendingConditionsForm = {
    selectedCategory: null,
    selectedField: null,
    selectedOperator: null,
    inputValue: "",
    checkedAdvancedMode: "",
    arrStrConditions: []
};

const MailStructureModule: IMailStructureForm = {
    dicCheckbox: [],
    arrMailStructureData: []
};

@Module({ dynamic: true, store: store, name: "alertTrigger" })
class AlertTriggerFormModule extends VuexModule implements IAlertTriggerFormModule {
    public alertTrigger: IAlertTrigger = null;
    public arrStatusForm: Array<IStatusComponent> = [];

    public component = {
        MainInformationModule: {
            clientInfo: {
                processName: "",
                selectedClient: null
            },
            selectedModule: new StyledFormRadioComponent(),
            selectedType: new StyledFormRadioComponent()
        },
        SendingConditionsModule: <ISendingConditionsForm>JSON.parse(JSON.stringify(SendingConditionsModule)),
        MailStructureModule: <IMailStructureForm>JSON.parse(JSON.stringify(MailStructureModule))
    };

    @MutationAction({ mutate: ["alertTrigger"] })
    public async fetchDataAlertTrigger(newVal: IAlertTrigger = null) {
        var returnData = { alertTrigger: null };
        if (newVal === null) {
            returnData.alertTrigger = {
                SendingConditions: [], MailRecipients: [], MailStructure: [], Attachments: [], MailDetails: { Body: null, Id: null, Subject: null, Alert: null },
                MainInformation: {
                    Id: null, Reference: null, ModuleType: null, AlertType: null, Name: null, Transac: null,
                    ActiveMinDate: null, ActiveMaxDate: null, IsComplete: null, IsActive: null, IsPaused: null, SQLCreationAuthor: null
                }
            };
        } else {
            if (newVal.MainInformation.ActiveMinDate != null) {
                newVal.MainInformation.ActiveMinDate = StaticCommonFunc.convertFromJsonDateToJsDate(String(newVal.MainInformation.ActiveMinDate));
            }
            if (newVal.MainInformation.ActiveMaxDate != null) {
                newVal.MainInformation.ActiveMaxDate = StaticCommonFunc.convertFromJsonDateToJsDate(String(newVal.MainInformation.ActiveMaxDate));
            }
            returnData.alertTrigger = newVal;
        }
        return returnData;
    }

    @Mutation
    private initStatusForm(initStatus: EInitStatus) {
        let tc = ETrigger;
        if (initStatus === EInitStatus.CREATE) {
            this.arrStatusForm = [
                { nameComponent: tc.MAIN_INFORMATION, status: "active", nextComponent: tc.SENDING_CONDITIONS },
                { nameComponent: tc.SENDING_CONDITIONS, status: "empty", nextComponent: tc.MAIL_RECIPIENTS },
                { nameComponent: tc.MAIL_RECIPIENTS, status: "empty", nextComponent: tc.MAIL_DETAILS },
                { nameComponent: tc.MAIL_DETAILS, status: "empty", nextComponent: tc.MAIL_STRUCTURE },
                { nameComponent: tc.MAIL_STRUCTURE, status: "empty", nextComponent: tc.ATTACHMENTS },
                { nameComponent: tc.ATTACHMENTS, status: "empty", nextComponent: tc.DATE_ACTIVATION_RANGE },
                { nameComponent: tc.DATE_ACTIVATION_RANGE, status: "empty", nextComponent: null }
            ];
        } else if (initStatus === EInitStatus.RECAP_EDIT) {
            this.arrStatusForm.forEach(item => {
                item.status = "filled";
            });
        } else if (initStatus === EInitStatus.RECAP_NON_EDIT) {
            this.arrStatusForm.forEach(item => {
                item.status = "recapNonEdit";
            });
            this.arrStatusForm.find(item => item.nameComponent === tc.DATE_ACTIVATION_RANGE).status = "recapSpecial";
        }
    }
    @Action({ commit: "initStatusForm" })
    public fetchInitStatusForm(initStatus: EInitStatus) { return initStatus; }

    @Action
    public processChangeStatus() {
        for (var i = 0; i < this.arrStatusForm.length; i++) {
            var iItem = this.arrStatusForm[i];
            if (iItem.status === "active") {
                for (var j = i + 1; j < this.arrStatusForm.length; j++) {
                    var jItem = this.arrStatusForm[j];
                    if (jItem.status === "empty") {
                        break;
                    } else {
                        jItem.status = "empty";
                    }
                }
                break;
            }
            var k = i + 1;
            if (iItem.status === "filled" && k < this.arrStatusForm.length) {
                var kItem = this.arrStatusForm[k];
                if (kItem.status === "empty") {
                    kItem.status = "active";
                    break;
                }
            }
        }
    }

    //Component: Main Information
    @Mutation
    private resetMainInformation() {
        this.component.MainInformationModule.clientInfo.processName = "";
        this.component.MainInformationModule.clientInfo.selectedClient = null;
        this.alertTrigger.MainInformation.AlertType = null;
        this.alertTrigger.MainInformation.ModuleType = null;
        this.alertTrigger.MainInformation.Name = "";
        this.alertTrigger.MainInformation.Transac = "";
    }
    @Action({ commit: "resetMainInformation" }) public onResetMainInformation() { }

    //Component: Sending Conditions
    @Mutation
    private resetSendingConditions() {
        this.alertTrigger.SendingConditions = [];
        this.component.SendingConditionsModule = JSON.parse(JSON.stringify(SendingConditionsModule));
    }
    @Action({ commit: "resetSendingConditions" }) public onResetSendingConditions() { }

    //Component: Mail Recipients
    @Mutation
    private resetMailRecipients() {
        this.alertTrigger.MailRecipients = [];
    }
    @Action({ commit: "resetMailRecipients" }) public onResetMailRecipients() { }

    //Component: Mail Details
    @Mutation
    private resetMailDetails() {
        this.alertTrigger.MailDetails = { Body: null, Id: null, Subject: null, Alert: null };
    }
    @Action({ commit: "resetMailDetails" }) public onResetMailDetails() { }

    //Component: Mail Structure
    /////////// reset data
    @Mutation
    private resetMailStructure() {
        this.alertTrigger.MailStructure = [];
        if (this.component.MailStructureModule.dicCheckbox.length > 0) {
            this.component.MailStructureModule.dicCheckbox.forEach(item => {
                item.AllSelected = false;
                item.Indeterminate = false;
                item.Selected = [];
            });
        }
    }
    @Action({ commit: "resetMailStructure" }) public onResetMailStructure() { }

    /////////// structure
    @Mutation
    private setDataArrMailStructure(newVal: Array<IStructureData>) {
        this.component.MailStructureModule.dicCheckbox = [];
        this.component.MailStructureModule.arrMailStructureData = newVal;
    }
    @Action
    public async fetchDataArrMailStructure() {
        return await Axios.get("/ClientAlerting/Alert/GetMailStructure").then((resp) => {
            if (resp.data.dResult) {
                this.context.commit("setDataArrMailStructure", resp.data.dDicStructure);
            }
            return <boolean>resp.data.dResult;
        });
    }

    //Component: Attachment
    @Mutation
    private resetAttachment() {
        this.alertTrigger.Attachments = [];
    }
    @Action({ commit: "resetAttachment" }) public onResetAttachment() { }

    // Component: Date Activation Range
    @Mutation
    private resetDateActivationRange() {
        this.alertTrigger.MainInformation.ActiveMinDate = null;
        this.alertTrigger.MainInformation.ActiveMaxDate = null;
    }
    @Action({ commit: "resetDateActivationRange" }) public onResetDateActivationRange() { }
}

export const AlertTriggerForm = getModule(AlertTriggerFormModule);
