import { Component, Vue } from "vue-property-decorator";
import { ConditionComponent } from "../condition";
import { ButtonIconAddComponent } from "../../../../common/button-icon-add";
import { AlertTriggerForm } from "../../../../../store/modules/alert-form";

const SendingConditionsModule = AlertTriggerForm.component.SendingConditionsModule;
@Component({
    template: require("./add-condition.html"),
    components: {
        "condition": ConditionComponent,
        "btn-icon-add": ButtonIconAddComponent
    }
})
export class AddConditionComponent extends Vue {

    private get arrConditions() {
        return AlertTriggerForm.alertTrigger.SendingConditions;
    }
    private set arrConditions(newVal) {
        AlertTriggerForm.alertTrigger.SendingConditions = newVal;
    }

    private get checkedAdvancedMode() {
        return SendingConditionsModule.checkedAdvancedMode;
    }
    private set checkedAdvancedMode(newVal) {
        SendingConditionsModule.checkedAdvancedMode = newVal;
    }

    private addNewCondition(): void {
        if (AlertTriggerForm.component.SendingConditionsModule.selectedCategory === null || SendingConditionsModule.selectedField === null
            || SendingConditionsModule.selectedOperator === null || SendingConditionsModule.inputValue.length === 0) {
            return;
        }
        this.arrConditions.push({
            Id: null,
            Alert: null,
            DestinationField: null,
            SourceField: {
                Category: SendingConditionsModule.selectedCategory,
                Id: SendingConditionsModule.selectedField.Id,
                Label: SendingConditionsModule.selectedField.Label
            },
            Operator: SendingConditionsModule.selectedOperator,
            Value: SendingConditionsModule.inputValue
        });
    }
}
