import { Component, Vue } from "vue-property-decorator";
import Vuelidate from "vuelidate";
import { requiredUnless } from "vuelidate/lib/validators";
import "./date-activation-range-section.scss";
import { FormHeaderComponent } from "../../../_shared/form-header";
import datepicker from "vuejs-datepicker";
import { GroupButtonForm } from "../../_shared/group-button-form";
import { AlertTriggerForm, ETrigger } from "../../../../store/modules/alert-form";
import { StaticCommonFunc } from "../../../../util/static-common-function";

Vue.use(Vuelidate);
Vue.use(require("vue-moment"));
@Component({
    template: require("./date-activation-range-section.html"),
    components: {
        "form-header": FormHeaderComponent,
        "datepicker": datepicker,
        "group-button-form": GroupButtonForm
    },
    validations: {
        frd: {
            required: requiredUnless("td_greater_frd")
        },
        td: {

        }
    }
})
export class DateActivationRangeComponent extends Vue {

    private get dateActivationRangeStatus() {
        return AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.DATE_ACTIVATION_RANGE).status;
    }
    private set dateActivationRangeStatus(newVal) {
        AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.DATE_ACTIVATION_RANGE).status = newVal;
    }

    private checkDisabled(): boolean {
        return AlertTriggerForm.arrStatusForm.some(item => item.nameComponent !== ETrigger.DATE_ACTIVATION_RANGE && item.status === "recapNonEdit");
    }

    get frd() {
        return AlertTriggerForm.alertTrigger.MainInformation.ActiveMinDate;
    }
    set frd(value) {
        AlertTriggerForm.alertTrigger.MainInformation.ActiveMinDate = value;
    }

    get td() {
        return AlertTriggerForm.alertTrigger.MainInformation.ActiveMaxDate;
    }
    set td(value) {
        AlertTriggerForm.alertTrigger.MainInformation.ActiveMaxDate = value;
    }

    get td_greaterNow() {
        if (this.td != null) {
            if (this.td < StaticCommonFunc.getNowDate() ||
                (this.frd !== null && this.frd > this.td))
                return false;
            else
                return true;
        }
        return true;
    }

    get frd_greaterNow() {
        if (this.frd != null) {
            if ((this.frd < StaticCommonFunc.getNowDate() && !this.checkDisabled()) ||
                (this.td !== null && this.frd > this.td) )
                return false;
            else
                return true;
        }
        return true;
    }

    textChanged() {
        this.$v.inputTest.$touch();
    }

    onSubmit() {
        if (AlertTriggerForm.alertTrigger.MainInformation !== null && this.frd_greaterNow && this.td_greaterNow) {
            this.dateActivationRangeStatus = "filled";
        }
    }

    onReset() {
        AlertTriggerForm.onResetDateActivationRange();
    }

    onUpdateStatus(newStatus: string) {
        this.dateActivationRangeStatus = newStatus;
    }
}
