import { Component, Vue, Watch } from "vue-property-decorator";
import { FormHeaderComponent } from "../../../_shared/form-header";
import { GroupButtonForm } from "../../_shared/group-button-form";
import { CustomCheckboxComponent, IDictionary, IValue } from "../custom-checkbox/_/custom-checkbox";
import { AlertTriggerForm, ETrigger } from "../../../../store/modules/alert-form";
import { IEmailStructure } from "../../../../model/io";
import { Root } from "../../../../store/root-module";
import "./mail-structure-form.scss";

const MailStructureModule = AlertTriggerForm.component.MailStructureModule;
@Component({
    template: require("./mail-structure-form.html"),
    components: {
        "form-header": FormHeaderComponent,
        "custom-checkbox": CustomCheckboxComponent,
        "group-button-form": GroupButtonForm
    },
})

export class MailStructureFormComponent extends Vue {
    private labelCategory: string = "label--category";
    private labelItem: string = "label--item";
    private dicData: Array<IDictionary> = [];
    private isCbChecked?: boolean = null;

    private get dicCheckbox() {
        return MailStructureModule.dicCheckbox;
    }
    private set dicCheckbox(newVal) {
        MailStructureModule.dicCheckbox = newVal;
    }

    private get arrayMailStructuresDictionary() {
        return MailStructureModule.arrMailStructureData;
    }

    private get arrayCategories() {
        return Root.arrCategories;
    }

    private get arrayFields() {
        return Root.arrFields;
    }

    private get mailStructureStatus() {
        return AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.MAIL_STRUCTURE).status;
    }
    private set mailStructureStatus(newVal) {
        AlertTriggerForm.arrStatusForm.find(item => item.nameComponent === ETrigger.MAIL_STRUCTURE).status = newVal;
    }

    private get mailStructureToSaved() {
        return AlertTriggerForm.alertTrigger.MailStructure;
    }
    private set mailStructureToSaved(newVal) {
        AlertTriggerForm.alertTrigger.MailStructure = newVal;
    }

    private get filterSelectedArr() {
        var filter = new Array<Object>();
        this.dicCheckbox.forEach((item, index) => {
            if (item.Selected.length > 0) {
                let itemLabels = item.Selected.map(ite => ite.Label);
                var displayStrItemLabels = itemLabels.length > 0
                    ? itemLabels.join(", ")
                    : " ";
                filter.push({
                    Category: item.Key.Label.toUpperCase(),
                    DisplayItems: displayStrItemLabels,
                });
            }
        });
        return filter;
    }

    @Watch("arrayMailStructuresDictionary")
    private initUI(): void {
        if (this.arrayMailStructuresDictionary.length > 0) {
            this.arrayMailStructuresDictionary.forEach(item => {
                // data load
                this.dicData.push({
                    Key: {
                        Id: item.Key.Id,
                        Label: item.Key.Label,
                        Abbr: item.Key.Code
                    },
                    Value: item.Value.map(m => <IValue>{
                        Id: m.Id,
                        Label: m.Label,
                        Abbr: m.Code
                    })
                });
                // data checked
                this.dicCheckbox.push({
                    Key: {
                        Id: item.Key.Id,
                        Label: item.Key.Label,
                        Abbr: item.Key.Code
                    },
                    Selected: [],
                    AllSelected: false,
                    Indeterminate: false
                });
            });
        }
    }

    private onUpdateStatus(newStatus: string) {
        this.mailStructureStatus = newStatus;
    }

    private onSubmit(event): void {
        this.isCbChecked = false;
        if (this.dicCheckbox.some(item => item.AllSelected || item.Indeterminate)) {
            this.mailStructureToSaved = [];
            this.dicCheckbox.filter(item => item.AllSelected || item.Indeterminate).forEach(item => {
                var arrDataPush = item.Selected.map(mItem => <IEmailStructure>{
                    Id: null,
                    AlertEmail: null,
                    Field: this.arrayFields.find(f => f.Id === mItem.Id)
                });

                this.mailStructureToSaved.push(...arrDataPush);
                this.mailStructureStatus = "filled";
                this.isCbChecked = true;
            });
        }
    }

    private onReset(): void {
        this.isCbChecked = null;
        AlertTriggerForm.onResetMailStructure();
    }

    private onMailPreview(): void {
        //let routeData = this.$router.resolve({ name: "mailTemplateAlert" });
        //window.open(routeData.href, "_blank");
    }

    @Watch("mailStructureStatus")
    @Watch("mailStructureToSaved")
    @Watch("dicCheckbox")
    private initRecapForm(): void {
        let that = this;
        if (["filled", "recapNonEdit"].indexOf(that.mailStructureStatus) > -1) {
            if (that.mailStructureToSaved !== null && that.mailStructureToSaved.length > 0) {
                if (that.dicCheckbox.length > 0 && !that.dicCheckbox.some(item => item.Selected.length > 0)) {
                    that.mailStructureToSaved.forEach(item => {
                        var selectedField = that.arrayFields.find(f => f.Id === item.Field.Id);
                        that.dicCheckbox.find(cb => cb.Key.Id === selectedField.Category.Id).Selected.push({
                            Id: selectedField.Id,
                            Label: selectedField.Label,
                            Abbr: selectedField.Code
                        });
                    });

                    that.dicCheckbox.filter(item => item.Selected.length > 0).forEach(item => {
                        var selectedDicData = that.dicData.find(dd => dd.Key.Id === item.Key.Id);
                        if (selectedDicData.Value.length === item.Selected.length) {
                            item.AllSelected = true;
                            item.Indeterminate = false;
                        } else if (selectedDicData.Value.length > item.Selected.length) {
                            item.AllSelected = false;
                            item.Indeterminate = true;
                        }
                    });
                }
            }
        }
    }

    private handleSelectCheckbox() {
        if (this.dicCheckbox.length > 0) {
            this.isCbChecked = this.dicCheckbox.some(item => item.AllSelected || item.Indeterminate);
        }
    }
}
